@tailwind base;
@tailwind components;
@tailwind utilities;

/* ============= Global Stylings ================= */
body{
  background-color: #fcf9f4;
}
:root {
  --active-color: #e01e5a;
  --secondary-color-two: rgb(69 201 241);
}
.text-black{
  color: black !important;
}
a:hover {
  background: transparent;
  color: var(--active-color);
}
.react-images__view {
  display: flex;
  justify-content: center;
  align-items: center;
}

.centerDisplay {
  display: flex;
  justify-content: center;
  align-items: center;
}

.horizontal-line {
  border-bottom: 3px solid #ffff;
  width: 20%;
  margin-bottom: 0.5rem;
  -webkit-animation: grow 2s forwards;
  animation: grow 2s forwards;
}

.submitBtn:hover{
  cursor: pointer;
  background-color: var(--active-color);
}

/* ================== Start of Loader ===================   */
.spinner:before {
  transform: rotateX(60deg) rotateY(45deg) rotateZ(45deg);
  animation: 750ms rotateBefore infinite linear reverse;
}
.spinner:after {
  transform: rotateX(240deg) rotateY(45deg) rotateZ(45deg);
  animation: 750ms rotateAfter infinite linear;
}
.spinner:before,
.spinner:after {
  box-sizing: border-box;
  content: '';
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -5em;
  margin-left: -5em;
  width: 10em;
  height: 10em;
  transform-style: preserve-3d;
  transform-origin: 50%;
  transform: rotateY(50%);
  perspective-origin: 50% 50%;
  perspective: 340px;
  background-size: 10em 10em;
  background-image: url(data:image/svg+xml;base64,);
}
@keyframes rotateBefore {
  from {
    transform: rotateX(60deg) rotateY(45deg) rotateZ(0deg);
  }
  to {
    transform: rotateX(60deg) rotateY(45deg) rotateZ(-360deg);
  }
}
@keyframes rotateAfter {
  from {
    transform: rotateX(240deg) rotateY(45deg) rotateZ(0deg);
  }
  to {
    transform: rotateX(240deg) rotateY(45deg) rotateZ(360deg);
  }
}
/* ==================== End of Loader =====================   */

/* ============= Global Stylings Ends Here ================= */

/* ============= Start of Navbar Styling ================= */
/* Navbar */
.webLogo {
  height: 4rem;
}
.webLogo:hover {
  background: transparent;
}
.navbar {
  max-height: 4.5rem !important;
}

.social-icons {
  display: inline-block !important;
  padding-right: 10px;
  padding-left: 10px;
}

.home-social-icons {
  position: relative !important;
  display: flex;
  width: 40px !important;
  height: 40px !important;
  text-align: center !important;
  font-size: 1.2em !important;
  line-height: 2em !important;
  background: rgba(230, 225, 225, 0.034) !important;
  border-radius: 50% !important;
  transition: 0.5s !important;
  justify-content: center;
  align-items: center;
}

.home-social-icons::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: var(--active-color);
  transition: 0.5s;
  transform: scale(0.9);
  z-index: -1;
}

.home-social-icons:hover::before {
  transform: scale(1.1);
  box-shadow: 0 0 15px var(--active-color);
}

.home-social-icons:hover {
  color: var(--active-color);
  box-shadow: 0 0 5px var(--active-color);
  text-shadow: 0 0 2px var(--active-color);
}

.home-social-icons::before {
  background: transparent;
}

/* Active and Inactive Links Stylings  */
/* Active Link */
.active {
  border-bottom: 1px solid var(--active-color);
  background-color: transparent;
  color: var(--active-color);
}
.activeLogo {
  border-bottom: 0px solid var(--active-color);
}

.inactiveLink {
  color: #787878;
}
.inactiveLink:hover {
  color: var(--active-color);
  text-shadow: 0px 0px 30px var(--active-color);
  -moz-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
}

/* ============= End of Navbar Styling ================= */

/* ============= Scroll Styling ================= */
/* Scroll to Top Styling */
.scroll-to-top {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  bottom: 200px !important;
  /*width: 40px; */
  /* height: 40px;*/
}
/* Scrollbar */
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #111827;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #007950;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #007950;
}

/* ============= Scroll Styling Ends ================= */

/* ======== Pages styling starts here */

/* =========== Common Header =========== */
.header_bg {
  background-color: rgb(0, 0, 0, 0.1);
  background-blend-mode: multiply;
  position: relative;
  margin-top: -4.5rem;
}
.headerText {
  animation: fadeIn;
  animation-duration: 4s;
}
.header_bg_opacity {
  animation: fadeIn;
  animation-duration: 3s;
}

/* ========== Film Youtube website ======= */

.YoutubeVideoGrid {
  width: 100%;
  height: 24rem;
}


/* ============= Gallery View ============= */
.gallery{
  -webkit-column-count: 3;
  -moz-column-count: 3;
  column-count: 3;
  -webkit-column-width: 33%;
  -moz-column-width: 33%;
  column-width: 33%;
  padding: 0 12px; 
}
.gallery .pics{
  -webkit-transition: all 350ms ease;
  transition: all 350ms ease;
  cursor: pointer;
  padding-bottom: 0.3rem;
}

.gallery .pics:hover{
  transform: scale(1.01);
  transition: transform 1s; 
}

.galleryImage{
  width: 100%;

}

.model{
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  transition: opacity .4s ease, visibility .4s ease, transform 0.5s ease-in-out;
  visibility: hidden;
  opacity: 0;
  transform: scale(0);
  overflow: hidden;
  z-index: 999;
}

.model.open{
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}

.model.open svg{
  position: fixed;
  top: 2rem;
  right: 4rem;
  width: 3rem;
  height: 2.7rem;
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
  cursor: pointer;
}
.model.open svg:hover{
  background-color: #1118272a;
  border-radius: 0.5rem;
  color: var(--active-color);
}

.model img{
  width: auto;
  max-width: 100%;
  max-height: 100%;
  height: auto;
  display: block;
  line-height: 0;
  box-sizing: border-box;
  padding: 20px 0 20px;
  margin: 0 auto
}

/* ======== Underline Decor ==========*/
.lineHighlight{
  width: 20%;
  margin-top: 1rem;
  border: black solid 0.045rem;
}

.bg_slider {
  --initialTextColor: #FFF;
  --slideTextColor: #272838;

  --initialBgColor: #272838;
  --slideBgColor: #FFF;

  color: var(--initialTextColor);

  background-image: linear-gradient(90deg,
      var(--initialBgColor) 0%,
      var(--initialBgColor) 50%,
      var(--slideBgColor) 50%,
      var(--slideBgColor) 100%);
  background-size: 400%;
  transition: background-position .3s cubic-bezier(.47, .1, 1, .63),
              color .2s linear;
  transition-delay: 0.0s,
                    0.15s;
}

.bg_slider:hover {
  color: var(--slideTextColor);
  cursor: pointer;
  background-position: -50% 100%;
}

/* ========== FOOTER Style */
.darkFooterLogo{
  height: 10rem;
  width: auto;
}
/* ============Home Screen ============= */
.homeBackgroundImage{
  background-color: #fcf9f4;
  height: calc(100vh - 4.5rem);
}

/* ===============END OF HOME SCREEN */

/* =========== About Us Section =============== */
.aboutUsVideoSection2{
  height: 31rem;
  object-fit: cover;
}
.readMoreLink{
  color: var(--secondary-color-two);
}
.headingColor{
  color: var(--secondary-color-two);
}
.readMoreLink:hover{
  cursor: pointer;
  color: var(--active-color);
}


/* =============HOME PAGE DESIGN ======================= */
.hoverHomeImage:hover{
  cursor: pointer;
}

/* ================ HOME PAGE DESIGN ENDS HERE ====================== */

/* ============= Responsive Design Starts ================= */
/* OrientationBased */
@media screen and (orientation: landscape) {
  .webLogo {
    height: 5.5rem;
  }
}
@media screen and (orientation: portrait) {
  .webLogo {
    height: 3.5rem;
    width: 10rem;
    position: absolute;
    right: 0.5rem;
  }
  .header_bg {
    margin-top: 0rem;
  }
}

/* For Desktop View */
@media screen and (min-width: 1024px) {
}

/* For Tablet View */
@media screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .webLogo {
    height: 4rem;
    width: 10rem;
    position: absolute;
    right: 0.5rem;
  }
}
/* For Mobile View */
@media screen and (max-width: 991px) {
  .header_bg {
    margin-top: 0rem;
  }
  .gallery{
    -webkit-column-count: 2;
    -moz-column-count: 2;
    column-count: 2;
  }
}
@media screen and (max-width: 991px) {
  .gallery{
    -webkit-column-count: 1;
    -moz-column-count: 1;
    column-count: 1;
    -webkit-column-width: 100%;
    -moz-column-width: 100%;
    column-width: 100%;
  }
}
/* ============= Responsive Design Ends ================= */
